<template>
    <div ref="mdshow" class="markdownshow" style="">
        <vue-markdown :source="source" :show="true" 
          :postrender="mdPost"
          @rendered="renderedHook"
          ></vue-markdown> 
          <!-- :prerender="mdPre" -->
    </div>
</template>

<script>
// import M from 'materialize-css'

//https://stackoverflow.com/questions/4009756/how-to-count-string-occurrence-in-string
function occurrences(string, subString, allowOverlapping) {

string += "";
subString += "";
if (subString.length <= 0) return (string.length + 1);

var n = 0,
    pos = 0,
    step = allowOverlapping ? 1 : subString.length;

while (true) {
    pos = string.indexOf(subString, pos);
    if (pos >= 0) {
        ++n;
        pos += step;
    } else break;
}
return n;
}

  const copyToClipboard = str => {
	  const el = document.createElement('textarea');
	  el.value = str;
	  document.body.appendChild(el);
	  el.select();
	  document.execCommand('copy');
	  document.body.removeChild(el);
	};

export default {
    name: '',
    props: ['source'],
    components: {

    },
    data(){
        return {
          materialbox: null,
        }
    },
    mounted(){

    },
    methods: {
      mdPre(text){
        return text
        let ret = '' 

        
        let finala = ''
        let totable = false
        let row = []
        let rows = []
        let indents = 0
        let cindent = 0
        let header = []
        for (let line of text.split("\n")){
          let rawline = line
          console.log('>>>rawline', rawline)
          if (line.replace(/\s/, '') == '' && totable){
            rows.push([...row])
            console.log('rows', rows)
            totable = false
            let maxrow = rows.reduce((g,c,i) => {
              return Math.max(g, c.length)
            }, 0)

            let afinal = rows.map(d => {
              d = ['', ...d.map(d => d.val), '']
              return d.join('|')
            })

            afinal = [
              ['', ...header.concat([...new Array(maxrow-header.length)].map(d => '')), ''].join('|'),
              ['', ...[...new Array(maxrow)].map(d => '---'), ''].join('|'),
              ...afinal,
            ]

            ret += afinal.join('\n')
            ret += "\n"
            continue
          }
          console.log(line, totable, row)
          if (line.includes('--totable')){
            header = line.replace('--totable', '').split(',').map(d => d.trim())
            totable = true
          }else if (totable){
            if (line.match('^[ ]*?\\*.*')){
              console.log('****')
              line = line.replace(/^(\s+)?(\*)(\s+)/, '$1 $3')
              let _indent = line.match('^[ ]+')[0].length
              let _indent2 = row.length*2 || _indent2
              let val = line.replace(/^[ ]+/, '')
              console.log('>>>line', line, '| _indent', _indent, '| cindent', cindent, '| _indent2', _indent2)
              if (_indent <= cindent){
                console.log('reduce')
                rows.push([...row])
                console.log('row before slice', row.map(d => d.val))
                // row = row.slice(0, row.length-(1+occurrences(row[row.length-1].line, '-.')))
                if (_indent-2 == 0){
                  row = [{
                    val,
                    line: rawline,
                  }]
                }else{
                  row = row.slice(0, _indent-2)
                }
                console.log('row after slice', row.map(d => d.val))
              }else{
                console.log('add')
                val = val.split('-.').map(d => d.trim()).map(d => {
                  console.log('d', d)
                  return{
                    val: d,
                    line: rawline,
                  }
                })
                row = row.concat(val)
              }
              cindent = _indent
            }else{
              console.log('----')
              let _indent = line.match('^[ ]+')[0].length
              let val = line.replace(/^[ ]+/, '')
              row[row.length-1].val += "<br>"+val
              row[row.length-1].line += "\n"+rawline
            }
          }else{
            console.log('hrere', '|', rawline, '|')
            ret += rawline+"\n"
          }
        }

        console.log('ret', ret)

        return ret
      },
      renderedHook(){
        if (this.$refs.mdshow || true){
          this.$nextTick(() => {
            console.log(this.$refs.mdshow)
            console.log('materialboxed', this.$refs.mdshow.querySelectorAll('.materialboxed'))
            this.M.Materialbox.init(this.$refs.mdshow.querySelectorAll('.materialboxed'))

              this.$el.querySelectorAll('p > code').forEach(function (el){
                  el.addEventListener('click', function (){
                      copyToClipboard(el.innerText)
                  })
              })
              this.$el.querySelectorAll('li > code').forEach(function (el){
                  el.addEventListener('click', function (){
                      copyToClipboard(el.innerText)
                  })
              })

            	this.$el.querySelectorAll('pre').forEach(function (el){
                // let button = $('<button>', { style: 'font-size:12px;display:none;position:absolute;top:3px;right:3px;z-index:10;color:black;'} ).text('copy');
                let button = document.createElement('button')
                button.setAttribute('style', 'font-size:12px;display:none;position:absolute;top:3px;right:3px;z-index:10;color:black;')
                button.innerText = 'copy'
                let txt = el.innerText;
                el.style.position = 'relative'
                button.addEventListener('click', function (){
                  copyToClipboard(txt);
                });
                el.addEventListener('mouseenter', function (){
                  button.style.display = 'block'
                })
                el.addEventListener('mouseleave', function (){
                  button.style.display = 'none'
                })
                el.prepend(button)
              });
          })
        }
      },
      mdPost(e){
        console.log('post', e)
        const regexp = new RegExp('<img.*?alt="(.*?)"[^/]*?\/>', 'gm')
        let match
        while ((match = regexp.exec(e)) !== null) {
          console.log('m', match)
          let rep = ''
          if (match[1]){
            let [w,h] = match[1].split('x')
            if (w)
              rep += ` width="${w}px" `
            if (h)
              rep += ` height="${h}px" `
            
            // e = e.replace(match[0], match[0].replace('/>', rep + ' />'))
          }
          e = e.replace(match[0], match[0].replace('/>', rep + ' class="materialboxed" style="display:inline-block;"/>'))
        }
        return e
      },
    },
}
</script>

<style>
.material-placeholder {
  display:inline-block;
}
</style>