import Vue from 'vue'
import App from './App.vue'
import router from './router'

import axios from 'axios'
import VueAxios from 'vue-axios'


import VuePrism from 'vue-prism'
Vue.use(VuePrism)

import 'prismjs/themes/prism.css'

import VueCodemirror from 'vue-codemirror'

Vue.prototype.isDev = !window.location.href.startsWith('https://')


import VueMarkdown from 'vue-markdown'

Vue.use(VueMarkdown)
Vue.component('vue-markdown', VueMarkdown)

// require styles
import 'codemirror/lib/codemirror.css'
Vue.use(VueCodemirror)

import M from 'materialize-css'

Vue.prototype.$col = 'crashcode3'

const copyToClipboard = str => {
  const el = document.createElement('textarea');
  el.value = str;
  document.body.appendChild(el);
  el.select();
  document.execCommand('copy');
  document.body.removeChild(el);
};

//https://stackoverflow.com/questions/19721439/download-json-object-as-a-file-from-browser
const forceJSONDownload = (data, filename='data.json') => {
  let dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(data));
  let dlAnchorElem = document.createElement('a');
  dlAnchorElem.setAttribute("href",     dataStr     );
  dlAnchorElem.setAttribute("download", filename);
  dlAnchorElem.click();
}

Vue.prototype.copyToClipboard = copyToClipboard
Vue.prototype.forceJSONDownload = forceJSONDownload

Vue.prototype.M = M

Vue.prototype.send_toast = (msg, status) => {
  if (status == 'ok'){
    M.toast({
      html: msg,
      classes: 'green darken-1 rounded'
    })
  }else if (status == 'err'){
    M.toast({
      html: msg,
      classes: 'red darken-1 rounded'
    })
  }else{
    M.toast({
      html: msg,
    })
  }
}


Vue.use(VueAxios, axios)


Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
