<template>
    <div class="">
        <a href="#!" @click="$emit('editpost')"
            v-if="isDev">Edit</a>
        <!-- <button class="btn" @click="copyToClipboard(`${this.location.origin}/notes?jsonurl=${btoa('http://automation:9000/get/crashcode2/c53dc12f2f9049bbb3248fc768710240')}`)">Copy embed</button> -->
        <template
            v-if="isDev">
            <br>
            <button class="btn" @click="copyEmbeded()">Copy embed</button>
            <button class="btn" @click="copyCodimd()">Copy codimd</button>
            <button class="btn" @click="downloadJSON()">Download JSON</button>
            <button class="btn" @click="copyDBLink()">Copy DBLink</button>
        </template>
        <h3 style="margin:0;padding;0;">{{ title }}</h3>
        <div style="width:30%;max-width:300px;float:left;">
            <NotesListL2
                :notelist="tree"
                :edit="false"
                @newfile="newFileHandler"
            />
        </div>
        <div style="width:69%;float:left;">
            <!-- <NoteEditL2 v-if="edit"
                :key="forceRefreshEdit"
                @saveFile="saveFileHandler"
                :treeObj="treeObj"/> -->
            <h4>{{this.treeObj.title}}</h4>
            <MKRender 
                v-if="treeSelected"
                :key="this.treeObj.title"
                :source="treeObj.content"/>
        </div>
    </div>
</template>

<script>
import MKRender from '../MKRender/main'
import NotesListL2 from '../NotesListL2/main'
import NoteEditL2 from '../NoteEditL2/main'

export default {
    name: '',
    props: ['noteSelected'],
    components: {
        MKRender,
        NotesListL2,
        NoteEditL2,
    },
    data(){
        return {
            forceRefreshEdit: 0,
            title: '',
            tree: [], // path, content
            treeSelected: null,
            treeObj: {
                title: '',
                content: '',
            },
        }
    },
    beforeMount(){
        this.title = this.noteSelected.title
        if (window.location.href.startsWith('https://')){
            this.title = this.noteSelected.title
            this.tree = this.noteSelected.tree
        }else{
            if (this.noteSelected.key){
                this.axios.get('http://automation:9000/get/crashcode2/'+this.noteSelected.key).then(rs => {
                    console.log(rs.data)
                    this.title = rs.data.title
                    this.tree = rs.data.tree || []
                })
            }
        }
        window.newFile = this.newFileHandler
        window.fileClick = this.newFileHandler
    },
    mounted(){
    },
    methods: {
        downloadJSON(){
            this.forceJSONDownload(this.noteSelected, this.noteSelected.key+'.json')
        },
        copyDBLink(){
            this.copyToClipboard(`http://automation:9000/get/crashcode2/${this.noteSelected.key}`)
        },
        copyCodimd(){
            let urlb = btoa('http://automation:9000/get/crashcode2/c53dc12f2f9049bbb3248fc768710240')
            let style = "width:100%;height:600px;border:0;"
            this.copyToClipboard(`<iframe project="crashcode2" style="${style}" src="${window.location.origin}/notes/?jsonurl=${urlb}"></iframe>`)
        },
        copyEmbeded(){
            let urlb = btoa('http://automation:9000/get/crashcode2/c53dc12f2f9049bbb3248fc768710240')
            this.copyToClipboard(`${window.location.origin}/notes?jsonurl=${urlb}`)
        },
        newFileHandler(title=''){
            console.log('new file handler', title)
            if (title != ''){
                let treeObj = this.tree.filter(d => d.title == title)
                if (treeObj.length == 1){
                    treeObj = treeObj[0]
                    console.log('treeobj', treeObj)
                    this.treeSelected = title
                    this.treeObj = treeObj
                    this.edit = true
                    this.forceRefreshEdit++
                    return
                }
            }
            let treeObj = {
                title: title,
                content: '',
            }
            // this.tree.push(treeObj)
            this.forceRefreshEdit++
            this.treeSelected = title
            this.treeObj = treeObj
            this.edit = true
        },
        savePostHandler(){
            const payload = {
                key: this.noteSelected.key,
                title: this.$refs.title.innerHTML,
                tree: this.tree,
            }
            this.axios.post('http://automation:9000/post/crashcode2', payload).then(rs => {
                // this.noteSelected.key = rs.data.key
                // this.noteSelected.title = rs.data.title
                this.send_toast('Save ok', 'ok')
            }).catch(err => {
                this.send_toast(err, 'err')
            })
        },
        saveFileHandler(obj){
            console.log('main save post', obj)
            this.tree = [
                ...this.tree.filter(d => d.title != obj.title),
                obj,
            ]
        }
    },
}
</script>

